.TabItems{
    max-width: 1050px;
    width: 100%;
    margin: 0px auto;
    display: flex;
    flex-wrap: wrap;
}

@media screen and (max-width: 1109px){
    .TabItems{
        max-width: 700px;
    }
}

@media screen and (max-width: 759px){
    .TabItems{
        max-width: 350px;
    }
}